footer {
  background: darken($footer-bg, 25%);
  color: white; 

  .grid-container {
    padding: 3rem;
  }

  h4 {
    @media screen and (max-width: 39.9375em) {
      font-size: 1.5rem;
    }
  }

  p {
    color: $footer-text-color;
  }

  .mailing-list {
    margin-bottom: 1.5rem;
  }
  .mailing-container {
    margin-bottom: 2rem;
  }

  @media screen and (min-width: 64em) {
    .about-container {
      padding-right: 2em;
    }
  }

  .about-section, .tag-section {
    margin-bottom: 2rem;

    a {
      color: $primary-color;
    }
  }

  .subscribe-button {
    background-color: $primary-color;

    &:hover {
     background-color: darken($primary-color, 5%);
     transition: color 0.3s ease-in;
    }
  }

  .fa-chevron-circle-up {
    font-size: 3rem;
    color: $footer-text-color;

     &:hover {
     color: lighten($footer-text-color, 15%);
     transition: color 0.3s ease-in;
    }
  }

  .copyright {
    padding: 1em 0;
    margin: 0;
    background: $footer-copyright-bg;
    color: $footer-copyright;
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .terms {
    background: $footer-terms-bg;
    padding: .5em 0;
    margin: 0;
    display: flex;
    justify-content: center;

    li {
      list-style-type: none;
      font-size: .9rem;
      font-weight: 700;

      &:first-child::after {
        content: '|';
        margin: 0 .5em;
        color: $white;
      }
  
      a {
        color: $white;
        &:hover {
          color: $white;
        }
        &:focus {
          color: $white;
        }
        &:active {
          color: $white;
        }
      }
    }
  }
}
